@import url(https://fonts.googleapis.com/css2?family=Arsenal&family=Poiret+One&family=Raleway:wght@411&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

/* NavBar */
.nav {
    overflow: hidden;
    background-color: #222222;
    position: -webkit-sticky;
    position: sticky;
    z-index: 1;
    top: 0;
    width: 100%;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.ul_nav {
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;

}

.li_nav a {
    display: block;
    color: #d3dfee;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
    font-family: 'Raleway', sans-serif;
    font-weight: bold;
    font-size: 18px;
}

.li_nav a:hover {
    color: #7b7a83bd;
    border-top: 2px solid #7b7a83bd;
}

@media screen and (max-width: 768px) {

    .li_nav a {
        padding: 12px 10px;
        font-size: 12px;
    }

}

/* Header */

.headerr {
    background-color: #000000;
    height: 720px;
}

.headinfo {
    text-align: center;
    left: 50%;
}

.header-name {
    display: -webkit-flex;
    display: flex;
    position: absolute;
    color: rgb(255, 255, 255);
    top: 40%;
    left: 28%;
    font-family: 'Arsenal', sans-serif;
    font-size: 65px;
}

.typed-text {
    display: -webkit-flex;
    display: flex;
    position: absolute;
    color: #838C95;
    top: 53%;
    left: 40%;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-size: 30px;
}

@media screen and (max-width: 768px) {
    .header-name {
        font-size: 2.2em;
        left: 10%;
    }

    .typed-text {
        font-size: 1.2em;
        top: 47%;
        left: 20%;
    }
}





/* About Me */

.aboutme {
    background-color: #222222;
    padding-top: 4em;
    padding-bottom: 4em;
    text-align: center;
    overflow: hidden;
}

.pro-p {
    text-align: center;
}

.about-pic {
    border-radius: 50%;
    max-width: 300px;
    max-height: 300px;
    margin-bottom: 10px;
}


.about-social {
    position: fixed;
    right: 8%;
    bottom: 5%;
}

.about-des {
    padding-top: 1em;
}

.abt {
    font-family: 'Arsenal', sans-serif;
    font-size: 30px;
    /* font-weight: bold; */
}

.about-h2 {
    color: #d3dfee;
    font-family: Verdana, Tahoma, sans-serif;
    font-style: bold;
}

.about-p {
    color: rgb(231, 229, 214);
    font-family: 'Raleway', sans-serif;
    font-size: 20px;
}

.pdf-download {
    background-color: #2a313a;
    border: none;
    color: rgb(255, 255, 255);
    padding: 10px 20px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    margin: 4px 2px;
    cursor: pointer;
    border-radius: 16px;

}

.pdf-download:hover {
    color: #fff;
    background-color: rgb(27, 113, 226);
    -webkit-transform: scale(1.25);
            transform: scale(1.25);
    transition: width 2s, height 1s, -webkit-transform 1s;
    transition: width 2s, height 1s, transform 1s;
    transition: width 2s, height 1s, transform 1s, -webkit-transform 1s;
}

.fa {
    padding-left: 10px;
    color: rgb(39, 110, 204);
    font-size: 35px;
    text-decoration: none;
}

.fa:hover {
    color: rgb(39, 191, 218);
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    transition: width 2s, height 1s, -webkit-transform 1s;
    transition: width 2s, height 1s, transform 1s;
    transition: width 2s, height 1s, transform 1s, -webkit-transform 1s;
}

@media screen and (max-width: 768px) {
    .fa {
        font-size: 28px;
    }
}

/* Project */

.project {
    text-align: center;
    background-color: rgb(231, 229, 214);
    padding-bottom: 7em;
    overflow: hidden;
    color: #000000;
}

.project-title {
    font-family: 'Raleway', sans-serif;
    font-size: 44px;
    padding-top: 2em;
}

.pro-subtitle {
    color: #626970;
}


.project-card {
    margin-left: 10%;
}

.pro-box {
    float: left;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    transition: 0.3s;
    width: 18em;
    height: 23em;
    background-color: #ffffff;
    color: #200303;
    font-family: 'Raleway', sans-serif;
    padding-top: 20px;
    justify-items: center;
    margin-top: 3em;
    margin-left: 30px;
}

.pro-box:hover {
    background-color: #222222;
    color: #ffffff;
    /* transform: rotate(-5deg) scale(1.1); */
    transition: .75s;
}

.pro-box-pic {
    max-width: 17em;
    max-height: 250px;
}

.box_title {
    font-weight: bold;
    font-size: 18px;
}

.box_des {
    padding-top: 1em;
}


/* Skills */
.skills {
    background-color: #ffffff;
    padding-top: 6em;
    overflow: hidden;
    margin-bottom: 5em;

}

.skills-title {
    font-size: 48px;
    padding-left: 3rem;
    color: #000524;
    font-family: 'Raleway', sans-serif;
    font-style: bolder;
}

.s-box {
    margin-left: 10%;
}

.skills-box {
    float: left;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    transition: 0.3s;
    width: 12em;
    background-color: #140808;
    border-radius: 5%;
    padding-top: 20px;
    margin-left: 3em;
    margin-top: 1em;
    justify-items: center;

}

.skills-box:hover {
    background-color: #4e2a00;
    box-shadow: 0 8px 16px 0 rgba(155, 76, 76, 0.822);

}


.skills-text {
    color: #fff;
    font-family: 'Raleway', sans-serif;
}

/* Experience */
.experience {
    padding-top: 3em;
    background-color: #222222;
    overflow: hidden;
    padding-bottom: 12em;

}

.exp-title {
    text-align: center;
    font-size: 48px;
    color: rgb(247, 247, 247);
    font-family: 'Raleway', sans-serif;
    font-style: bolder;
}

.exp-work {
    color: rgb(247, 247, 247);
    padding-top: 20px;
    font-family: Georgia, Times, 'Times New Roman', serif;
    font-size: 24px;
    margin-left: 20%;
}

.exp-des {
    text-align: right;
    padding-right: 50%;
}

.exp-info {
    color: rgb(221, 188, 188);
    font-size: 18px;
}


.work-section {
    transition: 0.3s;
    color: #200303;
    font-family: 'Raleway', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    border-radius: 5%;
    padding-top: 3em;
    padding-right: 40%;

}

@media screen and (max-width: 768px) {
    .exp-des {
        padding-right: 0;
    }

    .work-section {
        padding-right: 10%;
    }

}

.skill-pic {
    max-height: 100px;
    max-width: 120px;
    float: right;
}

.work-link {
    text-decoration: none;
    color: rgb(247, 247, 247);
}

.number {
    font-family: Arial, Helvetica, sans-serif;
}

/* Education */
.education {
    padding-top: 3em;
    background-color: #fff;
    margin-bottom: 10em;
    overflow: hidden;
    text-align: center;
}

.edu {
    font-size: 48px;
    color: rgb(49, 44, 44);
    font-family: 'Raleway', sans-serif;
    font-style: bolder;
}

.edu-work {
    color: rgb(0, 0, 0);
    font-family: 'Raleway', sans-serif;
    font-size: 24px;
}

.edu-info {
    color: rgb(131, 47, 47);
    font-size: 18px;
}

.edu-des {
    padding-top: 3em;
}

.edu-pic {
    max-height: 100px;
    max-width: 100px;
    margin-bottom: 1em;

}

.edu-link {
    text-decoration: none;
    color: #000524;
    font-size: 33px;
    font-style: bolder;
}

/* Contact Me */

.contact {
    background-color: #000000;
    color: #d3dfee;
    height: 650px;
    padding-top: 3em;
}

.contact-title {
    font-family: 'Raleway', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-size: 44px;
    padding-top: 3em;
    -webkit-align-content: center;
            align-content: center;
    text-align: center;
    padding: 10px;
}

.contact-form {
    background-color: #040a38;
    height: 360px;
    width: 60%;
    margin-left: 50px;
    margin-bottom: 30px;
    padding-top: 30px;
    padding-left: 30px;
    margin-left: 22%;
    border-radius: 10%;
}

.contact-text {
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-style: bold;
    font-size: 20px;
    margin-left: 45px;
}

.contact-box {
    float: right;
    margin-right: 50px;
}

.contact-submit {
    float: right;
    margin-top: 10px;
    margin-right: 25px;
    color: #fff;
    background-color: #2a315c;
    border: none;
    color: rgb(255, 255, 255);
    padding: 10px 20px;
    text-align: center;
    cursor: pointer;
    border-radius: 16px;
}

.contact-submit:hover {
    background-color: rgb(57, 10, 95);
    -webkit-transform: scale(1.20);
            transform: scale(1.20);
    transition: width 2s, height 1s, -webkit-transform 1s;
    transition: width 2s, height 1s, transform 1s;
    transition: width 2s, height 1s, transform 1s, -webkit-transform 1s;
}

.contact-footer {
    margin-top: 3em;
    color: rgb(231, 229, 214);
    font-family: 'Arsenal', Times, serif;
    background-color: #222222;
    height: 300px;
    text-align: center;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;

}

.foot-h2 {
    padding-top: 4rem;
    font-size: 44px;
}

.foot-p {
    font-size: 22px;
}

.foot-a {
    text-decoration: none;
    color: rgb(231, 229, 214);
}

.copyright {
    text-align: center;
}
